<template>
  <div>
            <!-- 面包屑导航区域 -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/About' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>权限管理</el-breadcrumb-item>
            <el-breadcrumb-item>角色列表</el-breadcrumb-item>
     </el-breadcrumb>

<!-- 卡片 -->
<el-card>
    <el-row>
        <el-col>
            <el-button  type="primary" @click="addDialogVisible=true">添加角色</el-button>
        </el-col>
    </el-row>

     <el-table :data="rolelist" border stripe>

         <el-table-column type="expand">
             <template slot-scope="scope">
                 <el-row :class="['bdbottom', i1===0?'bdtop':'','vcenter']" v-for="(item1,i1) in scope.row.children" :key="item1.id">
                     <!-- 一级 -->
                     <el-col :span="5">
                        <el-tag>{{item1.authName}}</el-tag>  
                        <i class="el-icon-caret-right"></i>
                     </el-col> 
                     <!-- 二级三级 -->
                      <el-col :span="19">
                          <!-- 嵌套渲染 -->
                          <el-row :class="[i2===0 ?'':'bdtop','vcenter']" v-for="(item2,i2) in item1.children" :key="item2.id">
                              <el-col :span="6">
                                   <el-tag type="success">{{item2.authName}}</el-tag>
                                     <i class="el-icon-caret-right"></i>
                              </el-col>
                              <el-col :span="18">
                                  <el-tag type="warning"  v-for="(item3,i3) in item2.children" :key="item3.id" closable @close='removetagId(scope.row,item3.id)'>
                                      {{item3.authName}}
                                  </el-tag>
                              </el-col>
                          </el-row>
                      </el-col>
                 </el-row>
                 <pre></pre>
             </template>
         </el-table-column>
           <el-table-column type="index" label="#"></el-table-column>
            
            <el-table-column label="角色名称" prop="roleName"></el-table-column>
             <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
              <el-table-column label="操作"  width="350px">
                <template slot-scope="scope">
                      <el-button size="mini" type="primary"  icon="el-icon-edit"  @click="aa(scope.row.id)">编辑</el-button>
                        <el-button size="mini" type="danger" icon="el-icon-delete"  @click="aa1(scope.row.id)">删除</el-button>
                          <el-button size="mini" type="warning"  icon="el-icon-setting" @click="showSetRighhtDialog(scope.row)">分配权限</el-button>
                               
                  </template>
              </el-table-column>
       </el-table>
</el-card>
<!-- 分配权限 -->
   <el-dialog
  title="分配权限"
  :visible.sync="setRightDialogVisible"
  width="50%"
  @close="setRightDialogClosed"
 >
<el-tree :data="rightslist" :props="treeProps" ref="treeRef" :default-checked-keys="defkeys" show-checkbox node-key="id" default-expand-all ></el-tree>
  <span slot="footer" class="dialog-footer">
    <el-button @click="setRightDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="alls()">确 定</el-button>
  </span>
</el-dialog>
   
 <!-- 添加用户的弹框 -->
    <el-dialog
        title="添加用户"
        :visible.sync="addDialogVisible "
        width="50%"
        @close="addDialogClosed"

        >
        <!-- 内容主题区域 -->
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px" class="demo-ruleForm">
            <el-form-item label="角色名称 " prop="roleName">
                <el-input v-model="addForm.roleName"></el-input>
            </el-form-item>
             <el-form-item label="角色描述 " prop="roleDesc">
                <el-input v-model="addForm.roleDesc"></el-input>
            </el-form-item>
           
        </el-form>
                <span slot="footer" class="dialog-footer">
            <el-button @click="addDialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="addUser">确 定</el-button>
        </span>
        </el-dialog>
          <!-- 修改用户对话框 -->
    <el-dialog
  title="修改角色"
  :visible.sync="editDialogVisidle"
  width="50%"
  @close="editDialogClosed"
 >
<el-form :model="editForm" :rules="editFormRules " ref="editFormRef" label-width="70px" >
  <el-form-item label="角色名称"  prop="roleName" >
    <el-input v-model="editForm.roleName" ></el-input>
  </el-form-item>
    <el-form-item label="角色描述"   prop="roleDesc">
    <el-input v-model="editForm.roleDesc" ></el-input>
  </el-form-item>

  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisidle = false">取 消</el-button>
    <el-button type="primary" @click="editUserinfo">确 定</el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
    data(){
        return{
            defkeys:[],
            treeProps:{
                label:"authName",
                children:"children"
            },

            rolelist:[],
                     addForm:{
            roleName:'',
            roleDesc:''
         
        },
        roleId:"",
        //控制分配
        setRightDialogVisible:false,
        rightslist:[],
        editForm:{
            roleName:'',
            roleDesc:''
        },
        id:"",
        all:[],
        showEditDialog:false,
        editDialogVisidle:false,
                addDialogVisible:false,
                    addFormRules:{
            roleName:[
                {required:true,message:'请输入角色名称',trigger:'blur'},
              
            ],
            roleDesc:[
                {required:true,message:'请输入角色描述',trigger:'blur'},
        
            ]
            
        },
        editFormRules:{
            roleName:[
                {required:true,message:'请输入角色名称',trigger:'blur'},
              
            ],
            roleDesc:[
                {required:true,message:'请输入角色描述',trigger:'blur'},
        
            ]
        }
        }
    },
    created(){
        this.getRolesList()
    },
    methods:{
       async getRolesList(){
           const {data:res}= await this.$http.get("roles")
           if(res.meta.status !==200){
               return this.$message.error("获取角色列表获取失败")
           }
           this.rolelist=res.data
           console.log( this.rolelist)

        },
         addDialogClosed(){
          this.$refs.addFormRef.resetFields()

            },
            addUser(){
 this.$refs.addFormRef.validate(async valid=>{
              console.log(valid)
               if(!valid) return
               //可以发送请求
            const {data:res}= await this.$http.post("roles",this.addForm)
            if(res.meta.status !==201){
                this.$message.error("添加用户失败")

            }
            this.$message.success("添加用户成功")
            this.addDialogVisible=false
            this.getRolesList()
          })
            },
        async aa(e){
                console.log(e)
                this.id=e
          const {data:res} =await this.$http.get('roles/'+e)
          if(res.meta.status !==200){ 
              return this.$message.error("查询用户失败！") 
          }
           this.editForm=res.data;
          this.editDialogVisidle=true
         
    // console.log(editForm)
                
            },
            editDialogClosed(){
                          this.$refs.editFormRef.resetFields()
            },
            editUserinfo(){

                  this.$refs.editFormRef.validate(async valid=>{
              console.log(valid) 
                  if(!valid) return 
                     //可以发送请求
            const {data:res}= await this.$http.put("roles/"+this.id,{
                roleName:this.editForm.roleName,
                roleDesc:this.editForm.roleDesc
            })
            if(res.meta.status !==200){
                return this.$message.error("更新用户失败")

            }
            //关闭对话框
            this.editDialogVisidle=false
            //刷新
            this.getRolesList()
            this.$message.success("更新用户成功")

              })
           
            },
             async aa1(id){
       const res= await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err=>err)
        // console.log(res)
        if(res !=="confirm"){
            return this.$message.info("已取消删除")
        }
        const {data:res1}=await this.$http.delete("roles/"+id)
        if(res1.meta.status !==200){
            return this.$message.error("删除用户失败")
        }
        this.$message.success("删除用户成功")
                     this.getRolesList()

      },
    async removetagId(role,rightId ){

         console.log(rightId)
const res= await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err=>err)
        // console.log(res)
        if(res !=="confirm"){
            return this.$message.info("已取消删除")
        }else{
                     const {data:res1}=await this.$http.delete(`roles/${role.id}/rights/${rightId} `)
        if(res1.meta.status !==200){
            return this.$message.error("删除用户失败")
        }
                                    // this.getRolesList()
                 role.children=res1.data 
                            
        
        // this.$message.success("删除用户成功")
                
        }

      },

      async showSetRighhtDialog(role){
          this.roleId=role.id
          //获取数据
          const {data:res} =await this.$http.get('rights/tree')
          console.log(res)
          if(res.meta.status !==200){
              return this.$message.error("获取数据失败")
          }
          //获取 道德权限数据 
          this.rightslist=res.data
          this.getLeafKeys(role,this.defkeys)
          this.setRightDialogVisible=true

      },
      getLeafKeys(node,arr){
          if(!node.children){
              return arr.push(node.id)
          }
          node.children.forEach(element => {
                this.getLeafKeys(element,arr)
          });
      },
      setRightDialogClosed(){
          this.defkeys=[]
      },
        async alls(){
          const keys=[...this.$refs.treeRef.getCheckedKeys(),
                    ...this.$refs.treeRef.getHalfCheckedKeys()]
                    const idStr=keys.join(",")
                   const {data:res}= await this.$http.post(`roles/${this.roleId}/rights`,{
                        rids:idStr
                    })
                    console.log(res)
                    if(res.meta.status !==200){
                        return this.$message.error("分配权限失败")
                    }
                    this.$message.success("分配权限成功")
                    this.getRolesList()
                    this.setRightDialogVisible=false
        
      }
    }
    
         
            
    
}
</script>

<style  scoped>
.el-tag{
    margin: 7px;
}
.bdtop{
    border-top: 1px solid #eee;
}
.bdbottom{
    border-bottom: 1px solid #eee;
}
.vcenter{
    display: flex;
    align-items: center;
}
</style>